.skillsBlock {
    background-color: #609898;
}

.skills {
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.title {
    background-color: #13e529;
}

@media (max-width: 1280px) {
    .skills {
        justify-content: space-evenly;
    }
}

@media (max-width: 900px) {
    .skills {
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
    }
}



